import {
  IonTabs,
  IonRouterOutlet,
  IonTabBar,
  IonTabButton,
  IonIcon,
  IonLabel,
} from "@ionic/react";
import PrivateRoute from "components/PrivateRoute";
import { personOutline, homeOutline, bagCheckOutline } from "ionicons/icons";
import { Redirect, useRouteMatch } from "react-router";
import Tab1 from "./Tab1";
import Tab2 from "./Tab2";
import Tab3 from "./Tab3";

const Tabs: React.FC = () => {
  const match = useRouteMatch();

  return (
    <IonTabs>
      <IonRouterOutlet>
        <PrivateRoute exact path={`${match.path}/tab1`}>
          <Tab1 />
        </PrivateRoute>
        <PrivateRoute exact path={`${match.path}/tab2`}>
          <Tab2 />
        </PrivateRoute>
        <PrivateRoute exact path={`${match.path}/tab3`}>
          <Tab3 />
        </PrivateRoute>
        <PrivateRoute exact path={match.path}>
          <Redirect to={`${match.path}/tab1`} />
        </PrivateRoute>
      </IonRouterOutlet>

      <IonTabBar slot="bottom">
        <IonTabButton
          tab="tab1"
          href={`${match.path}/tab1`}
          style={{ "--color-selected": "var(--ion-color-dark)" }}
        >
          <IonIcon icon={homeOutline} />
          <IonLabel>Início</IonLabel>
        </IonTabButton>
        <IonTabButton
          tab="tab2"
          href={`${match.path}/tab2`}
          style={{ "--color-selected": "var(--ion-color-dark)" }}
        >
          <IonIcon icon={bagCheckOutline} />
          <IonLabel>Vendas</IonLabel>
        </IonTabButton>
        <IonTabButton
          tab="tab3"
          href={`${match.path}/tab3`}
          style={{ "--color-selected": "var(--ion-color-dark)" }}
        >
          <IonIcon icon={personOutline} />
          <IonLabel>Perfil</IonLabel>
        </IonTabButton>
      </IonTabBar>
    </IonTabs>
  );
};

export default Tabs;
