import { getToken, removeToken } from "services/auth";

// Client API

const config = {
  headers: {
    "Content-Type": "application/json",
    Accept: "application/json",
    "App-Type": 2,
  },
};

const responseInterceptor = (response) => {
  if (response.status === 401) {
    removeToken();
    window.location.reload();
  }
};

export const fetcher = async (url, { headers, ...options } = {}) => {
  const token = getToken();
  if (token) config.headers.Authorization = `Bearer ${token}`;

  const response = await fetch(url, {
    headers: {
      ...config.headers,
      ...headers,
    },
    ...options,
  });

  responseInterceptor(response);

  const data = await response.json();

  if (!response.ok && response.status !== 401) throw new Error(data.message);

  return data;
};

export const get = (url) => fetcher(url);

export const post = (url, body) =>
  fetcher(url, {
    method: "POST",
    body: JSON.stringify(body),
  });

export const put = (url, body) =>
  fetcher(url, {
    method: "PUT",
    body: JSON.stringify(body),
  });

export const patch = (url, body) =>
  fetcher(url, {
    method: "PATCH",
    body: JSON.stringify(body),
  });

export const remove = (url) =>
  fetcher(url, {
    method: "DELETE",
  });

const client = (endpoint) => ({
  get: (id) => get(`${endpoint}/${id}`),
  getAll: () => get(`${endpoint}`),
  create: (body) => post(`${endpoint}`, body),
  post: (body) => post(`${endpoint}`, body),
  save: (id, body) => put(`${endpoint}/${id}`, body),
  update: (id, body) => patch(`${endpoint}/${id}`, body),
  remove: (id) => remove(`${endpoint}/${id}`),
});

export default client;
