import {
  IonBackButton,
  IonButton,
  IonButtons,
  IonCard,
  IonContent,
  IonHeader,
  IonInput,
  IonItem,
  IonLabel,
  IonPage,
  IonRow,
  IonText,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import { useUser } from "hooks/useUser";
import { FormEvent, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { User } from "types/User";

const SignIn: React.FC = () => {
  const { signIn, token } = useUser();
  const history = useHistory();

  // useEffect(() => {
  //   if (token) history.replace("/");
  // }, [history, token]);

  const [formData, setFormData] = useState<any>({
    email: "",
    password: "",
  });

  const submit = async (event: FormEvent, user: User) => {
    event.preventDefault();
    console.log(user);
    try {
      const response = await signIn(user);
      console.log({ response });
      history.replace("/");
    } catch (error: any) {
      alert(error.message);
    }
  };

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Kivid Afiliados</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Kivid Afiliados</IonTitle>
          </IonToolbar>
        </IonHeader>

        <IonCard>
          <form className="py-6" onSubmit={(e) => submit(e, formData)}>
            <IonText
              color="primary"
              className="block mt-0 mb-2 ion-padding-start text-2xl"
            >
              Login
            </IonText>
            <IonItem>
              <IonLabel position="floating">E-mail</IonLabel>
              <IonInput
                type="email"
                name="email"
                value={formData?.email}
                onIonChange={(e) =>
                  setFormData({ ...formData, email: e.detail.value! })
                }
                autocomplete="email"
                required
              />
            </IonItem>

            <IonItem>
              <IonLabel position="floating">Senha</IonLabel>
              <IonInput
                type="password"
                value={formData?.password}
                onIonChange={(e) =>
                  setFormData({ ...formData, password: e.detail.value! })
                }
                autocomplete="current-password"
                name="password"
                required
              />
            </IonItem>

            <div className="mt-8 px-4">
              <IonButton
                type="submit"
                expand="block"
                shape="round"
                size="large"
              >
                Entrar
              </IonButton>
            </div>
          </form>
        </IonCard>
      </IonContent>
    </IonPage>
  );
};

export default SignIn;
