import { User } from "types/User";
import clientApi, { post } from "lib/client-api";

// Users API
export const endpoint: any = {
  users: () => process.env.REACT_APP_APP_API_PATH + "/users",
  user: () => process.env.REACT_APP_APP_API_PATH + "/user",
  auth: () => process.env.REACT_APP_APP_API_PATH + "/auth",
  signIn: () => `${endpoint.auth()}/login`,
  signUp: () => `${endpoint.auth()}/sign-up`,
  signOut: () => `${endpoint.auth()}/logout`,
  sales: () => process.env.REACT_APP_APP_API_PATH + "/affiliate-invitations",
};

export const usersApi = clientApi(endpoint.users);
export const saveUser = async (user: User) => usersApi.get(user);
export const signIn = async (user: User) => post(endpoint.signIn(), user);
export const signUp = async (user: User) => post(endpoint.signUp(), user);
export const signOut = async () => post(endpoint.signOut(), {});
