import { useEffect, useState } from "react";
import {
  IonCard,
  IonCardContent,
  IonCol,
  IonContent,
  IonGrid,
  IonHeader,
  IonPage,
  IonRow,
  IonText,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import { useUser } from "hooks/useUser";

const Tab2: React.FC = () => {
  const { getSalesList } = useUser();
  const [sales, setSales] = useState<any[] | null>(null);

  const comission = 0.1;

  useEffect(() => {
    (async () => {
      try {
        const { data: sales } = await getSalesList();
        console.log({ sales });
        setSales(sales);
      } catch (error: any) {
        alert(error.message);
      }
    })();
  }, []);

  const getTotal = () => {
    const total = sales?.reduce((acc, cur) => {
      const price = comission * cur.plan_type.price;
      return acc + price;
    }, 0);
    return formatPrice(total);
  };

  const getReceived = (price: number) => {
    return formatPrice(price * comission);
  };

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat("pt-br", {
      style: "currency",
      currency: "BRL",
    }).format(price);
  };

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Vendas</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Vendas</IonTitle>
          </IonToolbar>
        </IonHeader>

        <section className="ion-padding-start ion-padding-end mt-4">
          <div>
            Quantidade de vendas: <b>{sales?.length || 0}</b>
          </div>
          <div className="mt-1">
            Total recebido:{" "}
            <IonText color="success">
              <b>{(sales?.length && getTotal()) || 0}</b>
            </IonText>
          </div>
        </section>

        <section className="my-8">
          {sales?.map((item) => (
            <IonCard key={item.id}>
              <IonCardContent className="p-4">
                <IonGrid>
                  <IonRow>
                    <IonCol size="6">
                      <p>Cliente</p>
                      <IonText color="dark" className="font-bold">
                        <p>{item.buyer.name}</p>
                      </IonText>
                    </IonCol>
                    <IonCol size="6">
                      <p>Plano comprado</p>
                      <IonText color="dark">
                        <p>{item.plan_type.name}</p>
                      </IonText>
                    </IonCol>
                  </IonRow>

                  <IonRow>
                    <IonCol size="6">
                      <p>Valor do plano</p>
                      <IonText color="dark">
                        <p>{formatPrice(item.plan_type.price)}</p>
                      </IonText>
                    </IonCol>
                    <IonCol size="6">
                      <p>Data da compra</p>
                      <IonText color="dark">
                        <p>{new Date(item.created_at).toLocaleDateString()}</p>
                      </IonText>
                    </IonCol>
                  </IonRow>

                  <IonRow>
                    <IonCol size="6">
                      <p>Valor do recebido</p>
                      <IonText color="success" className="font-bold">
                        <p>
                          {comission * 100}% (
                          {getReceived(item.plan_type.price)})
                        </p>
                      </IonText>
                    </IonCol>
                  </IonRow>
                </IonGrid>
              </IonCardContent>
            </IonCard>
          )) || (
            <div className="ion-padding">Você não tem vendas realizadas.</div>
          )}
        </section>
      </IonContent>
    </IonPage>
  );
};

export default Tab2;
