import {
  IonButton,
  IonContent,
  IonHeader,
  IonInput,
  IonItem,
  IonLabel,
  IonList,
  IonPage,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import { useUser } from "hooks/useUser";
import { useHistory } from "react-router";

const Tab3: React.FC = () => {
  const history = useHistory();
  const { logout, user } = useUser();

  const handleLogout = async () => {
    await logout();
    history.replace("/login");
  };

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Perfil</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Perfil</IonTitle>
          </IonToolbar>
        </IonHeader>

        <section>
          <IonList lines="none" className="mt-4">
            <IonItem>
              <IonLabel position="stacked">Nome</IonLabel>
              <IonInput readonly>{user?.name}</IonInput>
            </IonItem>
            <IonItem>
              <IonLabel position="stacked">E-mail</IonLabel>
              <IonInput readonly>{user?.email}</IonInput>
            </IonItem>
          </IonList>

          <IonButton
            color="danger"
            expand="block"
            fill="outline"
            className="mx-6 my-8"
            onClick={handleLogout}
          >
            Sair
          </IonButton>
        </section>
      </IonContent>
    </IonPage>
  );
};

export default Tab3;
