import useSWR from "swr";
import { fetcher, get } from "lib/client-api";
import * as Auth from "services/user";
import { endpoint } from "services/user";
import { User } from "types/User";
import { getToken, removeToken, setToken } from "services/auth";
import { useState } from "react";

export const useUser = (id: string = "") => {
  const token = getToken();

  const { data, error, mutate, isValidating } = useSWR(
    token ? endpoint.user() : null,
    fetcher,
    {
      // revalidateIfStale: false,
      // revalidateOnFocus: false,
      // revalidateOnReconnect: true,
      // refreshWhenHidden: false,
      // refreshWhenOffline: false,
      // refreshInterval: 0,
    }
  );

  const signIn = async (user: User) => {
    const data = await Auth.signIn(user);
    setToken(data.access_token);
    mutate();
    return data;
  };

  const signUp = async (user: User) => {
    await Auth.signUp(user);
    return user;
  };

  const logout = async () => {
    try {
      await Auth.signOut();
      // mutate();
    } catch (error) {
      console.log("Error", error);
    }
    removeToken();
    setToken(null);
  };

  const getSalesList = () => {
    return get(endpoint.sales());
  }

  return {
    data,
    mutate,
    user: data?.data,
    isValidating,
    isLoading: !error && !data?.data,
    token,
    signUp,
    signIn,
    logout,
    getSalesList
  };
};
