import {
  IonBackButton,
  IonButton,
  IonButtons,
  IonContent,
  IonHeader,
  IonInput,
  IonItem,
  IonLabel,
  IonPage,
  IonRow,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import { useUser } from "hooks/useUser";
import { FormEvent, useEffect, useState } from "react";
import { User } from "types/User";

const SignUp: React.FC = () => {
  const { signUp } = useUser();

  const [formData, setFormData] = useState({
    name: "",
    email: "",
    password: "",
  });

  // useEffect(() => {
  //   console.log(user);
  //   !!user && setFormData(user);
  // }, [user]);

  const submit = async (event: FormEvent, user: User) => {
    event.preventDefault();
    console.log(user);
    const userCreated = await signUp(user);
    console.log({ userCreated });
  };

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Cadastro</IonTitle>
          <IonButtons slot="start">
            <IonBackButton text="Login" />
          </IonButtons>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Cadastro</IonTitle>
          </IonToolbar>
        </IonHeader>

        <form
          className="ion-margin-top ion-margin-bottom"
          onSubmit={(e) => submit(e, formData)}
        >
          <IonItem>
            <IonLabel position="floating">Nome</IonLabel>
            <IonInput
              type="text"
              value={formData?.name}
              onIonChange={(e) =>
                setFormData({ ...formData, name: e.detail.value! })
              }
              autocapitalize="words"
              autocomplete="name"
              required
            />
          </IonItem>

          <IonItem>
            <IonLabel position="floating">E-mail</IonLabel>
            <IonInput
              type="email"
              value={formData?.email}
              onIonChange={(e) =>
                setFormData({ ...formData, email: e.detail.value! })
              }
              autocomplete="email"
              required
            />
          </IonItem>

          <IonItem>
            <IonLabel position="floating">Senha</IonLabel>
            <IonInput
              type="text"
              value={formData?.password}
              onIonChange={(e) =>
                setFormData({ ...formData, password: e.detail.value! })
              }
              autocomplete="new-password"
              required
            />
          </IonItem>

          <div className="ion-margin-top ion-padding">
            <IonButton type="submit" expand="block" shape="round" size="large">
              Cadastrar
            </IonButton>
            <IonRow className="ion-justify-content-center ion-margin-top">
              <IonButton
                type="button"
                fill="clear"
                shape="round"
                size="small"
                routerLink="/login"
                routerDirection="back"
              >
                Já tenho conta
              </IonButton>
            </IonRow>
          </div>
        </form>
      </IonContent>
    </IonPage>
  );
};

export default SignUp;
